header {
  position: absolute;
  width: 100%;
  //border-bottom: 1px solid #2c4f6f;
  z-index: 100;
  //box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
  //background-color: $primary-color;
  opacity: 0;
  transform: translateY(-100%);

  @include respond(tablet_mobile) {
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
  }
}

.nav-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
  //max-width: $max-width;

  .site-logo-text {
    font-size: 20px;
    color: #fff;

    &:hover {
      color: $secondary-color;
    }
  }

  .nav-links {
    display: flex;
    align-items: center;
    list-style-type: none;

    @include respond(tablet_mobile) {
      display: none;
    }
  }

  .nav-links {
    li {
      margin: 0 5px;

      a {
        color: #fff;
        padding: 15px 10px 10px 7px;
        display: flex;
        align-items: center;

        &:hover {
          color: $secondary-color;
        }

        &.active {
          color: $secondary-color;
          pointer-events: none;
          border-bottom: 1px solid $secondary-color;
          cursor: default;
        }

        .navlinks-icon {
          margin-right: 7px;
          font-size: 1.2em;
        }
      }
    }
  }

  .contact-btn {
    padding: 7px 20px;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 3px;
    background-color: $secondary-color-dark;
    border: 1px solid $secondary-color-dark;

    @include respond(tablet_mobile) {
      display: none;
    }

    &:hover {
      background-color: $secondary-color-dark;
      border: 1px solid #fff;
    }

    &.active {
      border: 1px solid $secondary-color-dark;
      background-color: $secondary-color-dark;
      pointer-events: none;
      cursor: default;
    }
  }

  //mobile menu
  .mobile-menu {
    display: none;
    cursor: pointer;

    @include respond(tablet_mobile) {
      display: block;
    }
    .hamburger {
      font-size: 2em;
      color: #fff;

      &:hover {
        color: $secondary-color;
      }
    }
  }

  .mobile-contact {
    display: none;
    cursor: pointer;

    @include respond(tablet_mobile) {
      display: block;
      pointer-events: all;
    }

    .icon {
      font-size: 2em;
      color: #fff;

      &:hover {
        color: $secondary-color;
      }
    }
  }

  .nav-links-mobile {
    display: none;
    flex-direction: column;
    background-color: $primary-color-dark;
    height: 100vh;
    width: 40%;
    position: absolute;
    left: 0;
    top: 0;
    //padding: 40px;
    transform: translateX(-100%);
    z-index: 100;

    @include respond(tablet_mobile) {
      display: flex;
    }

    @include respond(mobile) {
      width: 60%;
    }

    & li > a {
      color: #fff;
      display: block;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 40px;
      padding-right: 40px;
      font-size: 1.7em;
      display: flex;
      align-items: center;

      @include respond(mobile) {
        font-size: 1.2em;
        padding-left: 30px;
        padding-right: 30px;
      }

      &:hover {
        color: $secondary-color;
      }

      &.active {
        color: $secondary-color;
        pointer-events: none;
        cursor: default;
      }

      .navlinks-icon {
        margin-right: 25px;
        font-size: 1.2em;
      }
    }

    & > li {
      display: block;

      &:first-child {
        padding-top: 30px;
      }

      &.social-link-container {
        margin-top: auto;
        padding-bottom: 30px;
        justify-content: center;
        display: none;

        @include respond(mobile) {
          display: flex;
        }
      }

      .social-links {
        display: flex;
        align-items: center;
        list-style-type: none;

        li {
          margin-right: 10px;
        }
        a {
          display: block;
          color: #fff;
          padding-left: 5px;
          padding-right: 5px;

          &:hover {
            color: $secondary-color;
          }

          .social-icon {
            font-size: 1.2em;
          }
        }
      }
    }
  }

  .close-nav-links-mobile {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    display: none;
    transform: translateX(-100%);

    @include respond(tablet_mobile) {
      display: block;
    }
  }
}
