.contact {
  width: 100%;
  display: flex;
  height: 100vh;
  align-items: center;
  z-index: 10;
  justify-content: center;

  @include respond(mobile) {
    height: 100vh;
  }

  .form-container {
    width: 50%;
    //background-color: rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 10px;
    //margin-right: 30px;
    border: 1px solid #2c4f6f;
    opacity: 0;
    transform: translateY(10%);

    @include respond(tablet_mobile) {
      width: 100%;
      //margin-right: 0;
    }

    &:hover {
      border: 1px solid #eee;
      box-shadow: 0px 0px 10px 3px rgba($color: rgb(190, 190, 190), $alpha: 0.1);
    }

    form {
      margin-top: 10px;
    }

    .btn-container {
      margin-top: 20px;
    }
  }

  .map-container {
    width: 50%;
    height: 70%;
  }
}

.my-image {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15%;
  border: 1px solid #2c4f6f;
  border-radius: 100%;
  width: 300px;
  height: 300px;
  overflow: hidden;
  pointer-events: all;

  &:hover {
    border: 1px solid #eee;
    box-shadow: 0px 0px 10px 3px rgba($color: rgb(190, 190, 190), $alpha: 0.3);
  }

  img {
    width: 100%;
    height: 100%;
    border-radius: 100%;
    object-fit: cover;
    transform: scale(1.2);
  }
}
