.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  z-index: 10;
  overflow: hidden;

  @include respond(tablet) {
    width: 100%;
    align-items: center;
    text-align: center;
    overflow: hidden;
  }

  .wrapper {
    display: flex;
    flex-direction: column;
    width: 60%;

    @include respond(tablet_mobile) {
      //margin-top: 200px;
      text-align: center;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
  }

  .logo {
    height: 100px;
    margin-bottom: 80px;
    display: none;

    @include respond(tablet_mobile) {
      display: block;
      opacity: 0;
      transform: scale(0);
    }

    @include respond(mobile) {
      height: 70px;
      margin-bottom: 40px;
    }
  }

  .btn-container {
    margin-top: 40px;

    @include respond(mobile) {
      margin-top: 20px;
    }
  }

  .curve-bg {
    position: absolute;
    right: 0;
    //top: 100px;
    bottom: 0;
    height: 90%;
    width: auto;
    z-index: 0;
    pointer-events: none;
  }

  .heading,
  .text,
  .btn-container {
    opacity: 0;
    transform: translateY(20%);
  }

  .text {
    width: 80%;

    @include respond(tablet) {
      width: 60%;
    }

    @include respond(mobile) {
      width: 100%;
    }
  }
}

.my-logo-container {
  position: absolute;
  right: 10%;
  top: 50%;
  transform: translateY(-50%) scale(0);
  padding: 70px;
  border-radius: 100%;
  border: 1px solid #2c4f6f;
  background-color: $primary-color-dark;
  opacity: 0;
  pointer-events: all;
  left: auto;

  @include respond(tablet_mobile) {
    display: none;
  }

  &:hover {
    border: 1px solid #eee;
    box-shadow: 0px 0px 10px 3px rgba($color: rgb(190, 190, 190), $alpha: 0.1);
  }

  .logo {
    width: 200px;
    height: auto;
    pointer-events: none;

    @include respond(tablet) {
      width: 100px;
    }
  }
}
