.txtbox-container {
  position: relative;
  height: 50px;
  margin-bottom: 5px;
  overflow: hidden;
  padding-bottom: 2px;

  &.for-text-area {
    height: 100px;
  }

  &.active {
    &:after {
      transform: translateX(0);
    }
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 1px;
    background-color: #fff;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 2px;
    background-color: $secondary-color-dark;
    transform: translateX(-100%);
    transition: transform 0.2s ease-in;
  }

  input[type="text"],
  input[type="email"] {
    position: absolute;
    width: 100%;
    top: 20px;
    bottom: 20px;
    outline: none;
    height: 30px;
    font-size: 1em;
    border: none;
    background: transparent;
    color: #fff;
  }

  textarea {
    position: absolute;
    width: 100%;
    font-size: 1em;
    border: none;
    background: transparent;
    color: #fff;
    top: 27px;
    bottom: 20px;
    outline: none;
    flex: 1 1 100%;
  }

  label {
    position: absolute;
    top: 40%;
    color: #ccc;
    transition: font-size 0.1s ease, top 0.1s ease-in-out;
    font-weight: 200;
    letter-spacing: 1px;

    &.for-text-area {
      top: 15%;
    }

    &.active {
      top: 5%;
      transform: none;
      font-size: 0.9em;
      color: rgb(153, 153, 153);
    }
  }
}
