.skills {
  width: 100%;
  height: 100vh;
  //overflow: auto;

  @include respond(tablet_mobile) {
    height: auto;
  }

  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.5em;
    padding-top: 150px;

    @include respond(tablet) {
      grid-template-columns: 1fr 1fr;
      padding-top: 120px;
      padding-bottom: 90px;
    }

    @include respond(mobile) {
      grid-template-columns: 1fr;
      padding-top: 100px;
      padding-bottom: 50px;
    }

    .front-end {
      background-color: $primary-color;
      //border: none;
    }

    & > div {
      text-align: center;
      border: 1px solid #2c4f6f;
      border-radius: 5px;
      padding: 30px;
      opacity: 0;
      transform: translateY(10%);

      &:hover {
        border: 1px solid #eee;
        box-shadow: 0px 0px 10px 3px
          rgba($color: rgb(190, 190, 190), $alpha: 0.1);
      }

      .icon {
        font-size: 3em;
        color: $secondary-color;
      }

      h2 {
        font-weight: 700;
        margin: 20px 0;
      }

      h2,
      p,
      li {
        color: #fff;
        width: 100%;
      }

      .list-title {
        color: $secondary-color;
        margin: 20px 0;
      }

      ul {
        list-style-type: none;

        @include respond(tablet) {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          justify-content: center;
          li {
            padding-right: 5px;
            width: auto;
          }
        }
      }
    }
  }
}
