  //background-position: center;
  //background-repeat: no-repeat;
  //background-size: 120%;
}

body,
html {
  height: 100%;
}

.splash-container {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 101;
  top: 0;
  left: 0;
  overflow: hidden;
  pointer-events: none;
}
.splash {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: black;
}

.background-image-container {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
  top: 0;
  left: 0;
  pointer-events: none;
}

.background-image {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: scale(1.5);
  object-fit: cover;
}
