.projects {
  display: flex;
  align-items: center;
  //padding-top: 150px;
  width: 100%;
  height: 100vh;
  overflow: hidden;

  @include respond(mobile) {
    height: auto;
    padding-top: 100px;
    padding-bottom: 50px;
  }

  .grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.3em;

    @include respond(tablet) {
      grid-template-columns: 1fr 1fr;
    }
    @include respond(mobile) {
      grid-template-columns: 1fr;
    }

    .grid-item {
      //border: 1px solid #fff;
      min-height: 200px;
      position: relative;
      border-radius: 5px;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      overflow: hidden;
      box-shadow: 0px 0px 5px 2px rgba($color: #000000, $alpha: 0.1);
      opacity: 0;
      transform: scale(0);

      &:hover .details {
        transform: none;
      }

      .details {
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background-color: rgba(10, 16, 28, 0.95);
        padding: 10px;
        transform: translateY(100%);
        transition: transform 0.2s ease-in-out;
        text-align: center;

        h3 {
          color: #fff;
          margin-bottom: 20px;
          width: 100%;
        }
      }
    }
  }
}
