.container {
  max-width: $max-width;
  //width: 100%;
  margin-left: auto;
  margin-right: auto;
  //height: 100vh;
  //overflow: hidden;

  @include respond(laptop_desktop) {
    max-width: 90%;
  }

  @include respond(tablet_mobile) {
    max-width: 90%;
  }
}

.container-wrapper {
  max-width: $max-width;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100vh;
  pointer-events: none;

  @include respond(tablet) {
    width: 90%;
  }
}
