.about-page {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  z-index: 10;

  @include respond(tablet_mobile) {
    width: 100%;
    height: 60vh;
    align-items: center;
    text-align: center;
  }

  @include respond(mobile) {
    height: 80vh;
    justify-content: flex-start;
    padding-top: 150px;
  }

  .btn-container {
    margin-top: 40px;
  }

  .curve-bg {
    position: absolute;
    right: 0;
    //top: 100px;
    bottom: 0;
    height: 90%;
    width: auto;
    z-index: 0;
    pointer-events: none;
  }

  .heading,
  .text {
    opacity: 0;
    transform: translateY(20%);
  }

  .text {
    width: 80%;

    @include respond(mobile) {
      width: 100%;
    }
  }
}

.laptop {
  position: absolute;
  top: 50%;
  transform: translateY(-30%);
  right: 5%;
  max-width: 600px;
  pointer-events: none;
  z-index: 3;
  opacity: 0;

  @include respond(tablet_mobile) {
    top: 65%;
  }

  @include respond(mobile) {
    width: 90%;
    top: 70%;
  }
}

.home-side-div-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: 1;
  pointer-events: none;

  .triangle {
    height: 60vh;
    width: 60vh;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(30%, -50%) scale(1);
    background-color: rgba($color: #fff, $alpha: 0.1);
    opacity: 0;
    box-shadow: -2px 0px 5px rgba($color: #000000, $alpha: 0.2);
    border-radius: 10px;
    pointer-events: all;

    @include respond(tablet_mobile) {
      top: 65%;
      height: 45vh;
      width: 50vw;
    }

    @include respond(mobile) {
      height: 40vh;
      width: 60vw;
      top: 70%;
    }
  }
}
