.site-footer-wrapper {
  position: fixed;
  z-index: 99;
  bottom: 0;
  width: 100%;
  opacity: 0;
  transform: translateY(100%);

  @include respond(mobile) {
    position: relative;
    bottom: auto;
  }

  .site-footer {
    max-width: $max-width;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
  }

  .site-logo-svg {
    max-height: 35px;
  }

  .social-links {
    display: flex;
    align-items: center;
    list-style-type: none;

    @include respond(mobile) {
      display: none;
    }

    li {
      margin-right: 10px;
    }
    a {
      display: block;
      color: #fff;

      &:hover {
        color: $secondary-color;
      }

      .social-icon {
        font-size: 1.2em;
      }
    }
  }

  .bullet-container {
    display: flex;
    align-items: center;

    .bullet {
      height: 10px;
      width: 10px;
      border-radius: 100%;
      background-color: transparent;
      margin: 0 5px;
      border: 2px solid #fff;

      &:hover,
      &.active {
        background-color: $secondary-color;
        border: 2px solid $secondary-color;
      }
    }
  }
}
